
import {
  NgModule,
  Component,
  Directive,
  ElementRef,
  Input,
  AfterViewInit
} from "@angular/core";
import { getAttributes } from "@carbon/icon-helpers";


@Component({
	selector: "ibm-icon-ordinal",
  template: `
    <svg
      ibmIconOrdinal
      [size]="size"
      [ariaLabel]="ariaLabel"
      [ariaLabelledby]="ariaLabelledby"
      [ariaHidden]="ariaHidden"
      [title]="title"
      [isFocusable]="focusable"
      [attr.class]="innerClass">
    </svg>
  `
})
export class OrdinalComponent {
  @Input() ariaLabel: string;
  @Input() ariaLabelledby: string;
  @Input() ariaHidden: boolean;
  @Input() title: string;
  @Input() focusable: boolean = false;
  @Input() innerClass: string;
  @Input() size: string;
}



@Directive({
  selector: "[ibmIconOrdinal]"
})
export class OrdinalDirective implements AfterViewInit {
  static titleIdCounter = 0;

  @Input() ariaLabel: string;
  @Input() ariaLabelledby: string;
  @Input() ariaHidden: boolean;
  @Input() title: string;
  @Input() isFocusable: boolean = false;
  @Input() size: string;

  protected icons = {
    
  "16": {
    metadata: {"filename":"ordinal.svg","basename":"ordinal","size":16,"prefix":["16"],"descriptor":{"elem":"svg","attrs":{"xmlns":"http://www.w3.org/2000/svg","viewBox":"0 0 16 16","width":16,"height":16},"content":[{"elem":"path","attrs":{"d":"M1,13 L1,12 L3,12 L3,9 L5.999,9 L6,6 L9,6 L9,3 L13,3 L13,12 L15,12 L15,13 L1,13 Z M5.999,10 L4,10 L4,12 L5.999,12 L5.999,10 Z M8.999,7 L6.999,7 L6.999,9 L7,9 L7,12 L8.999,12 L8.999,7 Z M12,4 L10,4 L10,12 L12,12 L12,4 Z"}}],"name":"ordinal","size":16},"moduleName":"Ordinal16","outputOptions":{"file":"ts/ordinal/16.ts"},"namespace":"ordinal"},
    svg: `<svg focusable="false" preserveAspectRatio="xMidYMid meet" style="will-change: transform;" xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" aria-hidden="true"><path d="M1,13 L1,12 L3,12 L3,9 L5.999,9 L6,6 L9,6 L9,3 L13,3 L13,12 L15,12 L15,13 L1,13 Z M5.999,10 L4,10 L4,12 L5.999,12 L5.999,10 Z M8.999,7 L6.999,7 L6.999,9 L7,9 L7,12 L8.999,12 L8.999,7 Z M12,4 L10,4 L10,12 L12,12 L12,4 Z"></path></svg>`
  },

  };

  constructor(protected elementRef: ElementRef) {}

	ngAfterViewInit() {
    const svg = this.elementRef.nativeElement;
    svg.setAttribute("xmlns", "http://www.w3.org/2000/svg");

    const icon = this.icons[this.size]

    const domParser = new DOMParser();
    const rawSVG = icon.svg;
    const svgElement = domParser.parseFromString(rawSVG, "image/svg+xml").documentElement;

    let node = svgElement.firstChild;
    while (node) {
      // importNode makes a clone of the node
      // this ensures we keep looping over the nodes in the parsed document
      svg.appendChild(svg.ownerDocument.importNode(node, true));
      node = node.nextSibling;
    }

    const attributes = getAttributes({
      width: icon.metadata.descriptor.attrs.height,
      height: icon.metadata.descriptor.attrs.height,
      viewBox: icon.metadata.descriptor.attrs.viewBox,
      title: this.title,
      "aria-label": this.ariaLabel,
      "aria-labelledby": this.ariaLabelledby,
      "aria-hidden": this.ariaHidden,
      focusable: this.isFocusable.toString()
    });

    const attrKeys = Object.keys(attributes);
    for (let i = 0; i < attrKeys.length; i++) {
      const key = attrKeys[i];
      const value = attributes[key];
      if (key === "title") {
        continue;
      }
      if (value) {
        svg.setAttribute(key, value);
      }
    }

    if (attributes.title) {
      const title = document.createElement("title");
      title.textContent = attributes.title;
      OrdinalDirective.titleIdCounter++;
      title.setAttribute("id", `ordinal-${OrdinalDirective.titleIdCounter}`);
      svg.appendChild(title);
      svg.setAttribute("aria-labelledby", `ordinal-${OrdinalDirective.titleIdCounter}`);
    }
	}
}


@NgModule({
  declarations: [
    
  OrdinalComponent,
  OrdinalDirective,

  ],
  exports: [
    
  OrdinalComponent,
  OrdinalDirective,

  ]
})
export class OrdinalModule {}
